.dt
{
    height: 80vh;
    width: 900px;
    background-color: aliceblue;
    margin-left: 15%;
    justify-content: center;
    align-items: center;
    display: flex;
    padding-top:0;
}

table 
{
    width: 800px;
    height: 60vh;
    text-align: center;
    border: 2px solid rgb(22, 22, 12);
}
th 
{
    border: 1px solid rgb(22, 22, 12);
}
td 
{
    border: 1px solid rgb(22, 22, 12);
}
button 
{
   
    border: none;
    cursor: pointer;
    justify-content: space-between;

}
button svg{
    position: relative;
    top: 3px;
    color: cornflowerblue;
   
}