.contnr {
    display: inline-block;
    width: 500px;
    height: 300px;
    
    margin-left: 40%;
    background-color: aliceblue;
}

.contnr ul 
{
    margin-top: 10%;
    align-items: center;
    justify-content: space-evenly;
    padding: 10px;
    text-decoration: none;
    list-style-type: none;
}

.contnr li 
{
    align-items: center;
    justify-content: space-evenly;

    text-align: center;
}
