

body
{
    margin: 0;
    padding: 0;
    background-image: url(images.png);
    background-repeat: no-repeat;
    overflow: hidden;
    background-size: cover;
    height: 100vh;
    

}


.Header 
{
    background-color: rgb(22, 101, 190);
    color: aliceblue;
    height: 30px;
    padding: 7px;
}
.Header h3 {
    display: inline;
    padding-top: 15px;
}
.Header ul
{
    display: inline;
    align-items: center;
    padding-left: 50%;
    
  
}
.Header ul li 
{
    align-items: center;
 display: inline;
 justify-content: space-between;
 padding-left: 30px;
 
}

