

.lgin 
{
    height: 350px;
    width: 250px;
    
    align-items: center;
    margin-left: 40%;
    margin-top: 40px;
    box-sizing: border-box;
    border-radius: 5px solid  rgb(64, 16, 236); ;
    
    background-color: transparent;
    color: rgb(17, 81, 243) ;
    box-shadow: 0 0 20;


}
.lgin:hover 
{
    background-color: rgba(8, 50, 90, 0.911);
    box-shadow: 0 0 20;
    cursor: pointer;
}


  
.lgin label 
{
    align-items: center;
    display: inline-block;
    padding: 5px;
    margin-left: 30%;
    margin-top: 20px;

}

.lgin input 
{
    align-items: center;
    display: inline-block;
    padding: 5px;
    margin-left: 15%;
    margin-top: 20px;
    height: 25px;
    box-sizing: border-box;
    border-radius: 8px solid  ;
    border-color: rgb(58, 12, 221);
}

.lgin button 
{
    margin-top: 50px;
    margin-left: 30%;
    border-radius: 8px solid ;
    border-color: rgb(58, 12, 221);
    box-sizing: border-box;
}
.lgin p 
{
    
    display: inline-block;
    justify-content: center;
    margin-left: 20%;
    align-items: center;
}
    
a .Lnk 
{
    padding-left: 25px;
}